const mutations = {
	nextMonthCard(state) {
		if (state.currentIndex < state.monthList.length - 1) {
			state.currentIndex++
		}
	},
	
	prevMonthCard(state) {
		if (state.currentIndex > 0) {
			state.currentIndex--
		}
	},
	
	// currentIndex
	changeCurrentIndex(state, selectedMonth) {
		state.currentIndex = selectedMonth
	}
}

export default mutations